                <td colspan="2"><b>NR覆盖率(SS-RSRP>=-100&amp;SS-SINR> =0：</b>{{countData.wxzbBean?countData.wxzbBean['nrRsrp100Sinr0Rate']:''}}</td>
              </tr>
              <tr>
                <td colspan="2"><b>SS SINR连续质差里程占比：</b>{{countData.wxzbBean?countData.wxzbBean['linkSsSinrTimeDistanceTotalRate']:''}}</td>
                <td colspan="2"><b>SS RSRP连续弱覆盖里程占比：</b>{{countData.wxzbBean?countData.wxzbBean['linkSsRsrpTimeDistanceTotalRate']:''}}</td>
              </tr>
              <tr>
                <td colspan="2"><b>SINR连续质差里程占比：</b>{{countData.wxzbBean?countData.wxzbBean['linkSinrTimeDistanceTotalRate']:''}}</td>
                <td colspan="2"><b>RSRP连续弱覆盖里程占比：</b>{{countData.wxzbBean?countData.wxzbBean['linkRsrpTimeDistanceTotalRate']:''}}</td>
              </tr>
              <tr>
                <td colspan="2"><b>竞对测试RSRP平均值（服务商）如CMCC,-90：</b>{{countData.wxzbBean?countData.wxzbBean['comptestRsrp0']:''}}</td>
                <td colspan="2"><b>竞对测试RSRP平均值（竞对商）如CTC,-90：</b>{{countData.wxzbBean?countData.wxzbBean['comptestRsrp1']:''}}</td>
              </tr>
              <tr>
                <td colspan="4"><b>竞对测试RSRP平均值（竞对商）如CUC,-90：</b>{{countData.wxzbBean?countData.wxzbBean['comptestRsrp2']:''}}</td>
              </tr>
            </table>
          </div>
        </a-col>
        <a-col :span="15">
          <a-row>
            <a-col :span="12">
              <div id="wireless1" style="height:40vh;"></div>
