                <td><b>上传总量：</b>{{countData.ftpzbBean?countData.ftpzbBean['cszl']:''}}</td>
                <td><b>ftp请求次数：</b>{{countData.ftpzbBean?countData.ftpzbBean['pppRequestCount']:''}}</td>
                <td><b>业务时长：</b>{{countData.ftpzbBean?countData.ftpzbBean['testTime']:''}}</td>
              </tr>
              <tr>
                <td><b>ftp请求失败次数：</b>{{countData.ftpzbBean?countData.ftpzbBean['pppRequestFaileCount']:''}}</td>
                <td><b>下载平均速率：</b>{{countData.ftpzbBean?countData.ftpzbBean['downloadAvg']:''}}</td>
                <td><b>下载最大速率：</b>{{countData.ftpzbBean?countData.ftpzbBean['downloadMax']:''}}</td>
                <td><b>上传平均速率：</b>{{countData.ftpzbBean?countData.ftpzbBean['uploadAvg']:''}}</td>
              </tr>
              <tr>
                <td colspan="4"><b>上传最大速率：</b>{{countData.ftpzbBean?countData.ftpzbBean['uploadMax']:''}}</td>
              </tr>
            </table>
          </div>
        </a-col>
        <a-col :span="15">
          <a-row>
            <a-col :span="12">
              <div id="ftpchart1" style="height:35vh;"></div>
            </a-col>
            <a-col :span="12">
              <div id="ftpchart2" style="height:35vh;"></div>
            </a-col>
          </a-row>
        </a-col>
      </a-row>
    </a-card>

    <a-card size="small" title="语音指标">
      <a-row type="flex" justify="space-around" :gutter="8">
        <a-col :span="9"  class="column_kpi">
          <div>
            <table>
              <tr>
                <td><b>呼叫时延最大：</b>{{countData.yyzbbean?countData.yyzbbean['hjsyd']:''}}</td>
                <td><b>呼叫时延最小：</b>{{countData.yyzbbean?countData.yyzbbean['hjsyx']:''}}</td>
                <td><b>呼叫时延平均：</b>{{countData.yyzbbean?countData.yyzbbean['hjsyp']:''}}</td>
                <td><b>主叫次数：</b>{{countData.yyzbbean?countData.yyzbbean['zjcs']:''}}</td>
              </tr>
              <tr>
                <td><b>被叫次数：</b>{{countData.yyzbbean?countData.yyzbbean['bjcs']:''}}</td>
                <td><b>未接通次数：</b>{{countData.yyzbbean?countData.yyzbbean['wjtcs']:''}}</td>
                <td><b>掉话次数：</b>{{countData.yyzbbean?countData.yyzbbean['dhcs']:''}}</td>
                <td><b>接通率：</b>{{countData.yyzbbean?countData.yyzbbean['jtl']:''}}</td>
              </tr>
              <tr>
                <td colspan="4"><b>掉话率：</b>{{countData.yyzbbean?countData.yyzbbean['dhl']:''}}</td>
              </tr>
            </table>
          </div>
        </a-col>
        <a-col :span="15">
          <a-row>
            <a-col :span="12">
              <div id="volteChart1" style="height:35vh;"></div>
            </a-col>
            <a-col :span="12">
              <div id="volteChart2" style="height:35vh;"></div>
            </a-col>
          </a-row>
        </a-col>
      </a-row>
    </a-card>

    <a-card size="small" title="Ping/Http指标">
      <a-row type="flex" justify="space-around" :gutter="8">
        <a-col :span="9" class="column_kpi">
          <div >
            <h4 style="text-align: left;margin: 10px 0px">Ping指标：</h4>
            <table>
              <tr>
                <td><b>请求次数：</b>{{countData.pingzbBean?countData.pingzbBean['qqcs']:''}}</td>
                <td><b>成功次数：</b>{{countData.pingzbBean?countData.pingzbBean['cgcs']:''}}</td>
                <td><b>成功率：</b>{{countData.pingzbBean?countData.pingzbBean['cgl']:''}}</td>
                <td><b>最大时延：</b>{{countData.pingzbBean?countData.pingzbBean['zdsy']:''}}</td>
              </tr>
              <tr>
                <td colspan="2"><b>最小时延：</b>{{countData.pingzbBean?countData.pingzbBean['zxsy']:''}}</td>
                <td colspan="2"><b>平均时延：</b>{{countData.pingzbBean?countData.pingzbBean['pjsy']:''}}</td>
              </tr>
            </table>
          </div>
          <div >
            <h4 style="text-align: left;margin: 10px 0px">Http指标：</h4>
            <table>
              <tr>
                <td><b>请求次数：</b>{{countData.httpzbBean?countData.httpzbBean['qqcs']:''}}</td>
                <td><b>成功次数：</b>{{countData.httpzbBean?countData.httpzbBean['cgcs']:''}}</td>
                <td><b>成功率：</b>{{countData.httpzbBean?countData.httpzbBean['cgl']:''}}</td>
                <td><b>最大时延：</b>{{countData.httpzbBean?countData.httpzbBean['zdsy']:''}}</td>
              </tr>
              <tr>
                <td colspan="2"><b>最小时延：</b>{{countData.httpzbBean?countData.httpzbBean['zxsy']:''}}</td>
                <td colspan="2"><b>平均时延：</b>{{countData.httpzbBean?countData.httpzbBean['pjsy']:''}}</td>
              </tr>
            </table>
          </div>
        </a-col>
        <a-col :span="15">
          <a-row>
            <a-col :span="12">
              <div id="httpChart1" style="height:35vh;"></div>
            </a-col>
            <a-col :span="12">
